module.exports = "/*\n\n#search-params ul{\n  display: block;\n  background-color: #F9F9F9 !important;\n  margin-bottom: 0;\n  padding: 15px;\n  font-size: 10pt;\n  border-radius: 0.25em;\n}\n\n#search-params ul li{\n  display: inline-block;\n  font-weight: 700;\n  margin-right: 10px;\n}\n\n#search-params ul li i{\n  cursor: pointer;\n}\n\n#search-params ul li i:hover{\n  color: black;\n  font-size: 11pt;\n}\n\ndiv#search-params{\n  cursor: pointer;\n}\n\n#collapseFilter4{\n  text-align: right;\n  padding-top: 15px;\n  padding-bottom: 15px;\n  cursor: pointer;\n}\n\n*/\n\n.not-selected {\n  color: #b8bcbf;\n}\n\n/* SWITCH */\n\n.switch {\n  position: relative;\n  display: inline-block;\n  width: 40px;\n  height: 23px;\n  margin: 0 20px 2px 20px;\n}\n\n.switch input {\n  opacity: 0;\n  width: 0;\n  height: 0;\n}\n\n.slider {\n  position: absolute;\n  cursor: pointer;\n  top: 0;\n  left: 0;\n  right: 0;\n  bottom: 0;\n  background-color: #2196F3;\n  transition: .4s;\n}\n\n.slider:before {\n  position: absolute;\n  content: \"\";\n  height: 16px;\n  width: 16px;\n  left: 4px;\n  bottom: 4px;\n  background-color: white;\n  transition: .4s;\n}\n\ninput:focus + .slider {\n  box-shadow: 0 0 1px #2196F3;\n}\n\ninput:checked + .slider:before {\n  -webkit-transform: translateX(16px);\n  transform: translateX(16px);\n}\n\n/* Rounded sliders */\n\n.slider.round {\n  border-radius: 34px;\n}\n\n.slider.round:before {\n  border-radius: 50%;\n}\n"